@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&display=swap);
  
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Karla", sans-serif;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#959595+0,0d0d0d+46,010101+50,0a0a0a+53,4e4e4e+76,383838+87,1b1b1b+100;Black+Gloss+Pipe */
  background-image: linear-gradient(
    -225deg,
    #fdff71 0%,
    #fdff83 48%,
    #fffd93 100%
  );
  /* Add the blur effect */

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
    width: 35%;
    background: rgb(248, 240, 132);
    opacity: 1;
    border-style: ridge;
    border-color: black;
}

.bg-img {
    background-image: url(/React-Portfolio/static/media/navWallpaper4.9a299293.jpg);
}

.nav-link {
    font-family: "East Sea Dokdo", cursive;
    color: rgb(0, 0, 0);
    font-size: 25px;
    background-color: rgb(253, 255, 145);
    opacity: 0.6;
    border-radius: 20px;
}

.nav-theme {
    background-color: rgb(253, 255, 145);
    font-size: 20px;
}


.animate-navbar {
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.5s ease-in-out;
}

.navbar-toggler-icon {
    background-image: url(/React-Portfolio/static/media/navbarIcon.fcabfdd5.png);
    border: 1px solid transparent;
}

@keyframes moveDown {
    from {
        transform: translateY(-5rem);
    } 
    to {
        transform: translateY(0rem);
    }
}
.custom-img {
    height: 100vh;
    object-fit: cover;
}
span {
    background: rgba(250, 250, 250, 0.3);
    
}
.accordian-main {
    cursor: pointer;
    display: inline-block;
    -webkit-text-decoration: none;
    text-decoration: none;
    font-size: 14px;
    background-color: rgb(19, 126, 15);
    border: none;
    border-radius: 4px;

    padding: 10px;
    color: rgb(255, 255, 255);
}

.list-styles {
    list-style-type: circle;
}

.image-style1 {
    width: 3em;
}

.font-details {
    font-family: "East Sea Dokdo", cursive;
    color: black;
}

.card {
    background-image: url(/React-Portfolio/static/media/cardBackground.24e1d6c3.jpg);
    background-size: cover;
}
.about {
    background-color: transparent;
    font-size: 1.1em;
}

.profile {
    width: 75%;
    height: 75%;
}

.my-details {
    background-color: rgba(255, 255, 255, 0.747);
}

.title {
    font-size: 3em;
    font-weight: 500;
    font-family: 'East Sea Dokdo', cursive;
    color: rgb(0, 0, 0);
}


.focus {
    transition: transform 0.3s;
}

.focus:hover {
    transform: scale(1.05);
}

.card-title {
    font-size: 2em;
    font-weight: 700;
}

.image-style {
    width: 3em;
}

.font-details {
    font-size: 3em;
    font-weight: 500;
    font-family: "East Sea Dokdo", cursive;
    color: #000000;
}
.contact-jumbotron {
    background-image: url(/React-Portfolio/static/media/contactBanana.dcdc328b.jpg);
    background-size: cover;
    background-repeat: no-repeat;

    font-family: "East Sea Dokdo", cursive;
    color: black;
}

.btn {
    background-color: rgb(19, 126, 15);
    border: none;
    border-radius: 4px;

    color: beige;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
}

.container-box {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}

.particles-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.footer-style {
    background-color: rgb(0, 184, 70);
    color: #fff;
    font-size: 1.5em;
    border: 2px solid #101018f8;
    border-top-left-radius: 3px;
    background-image: url(/React-Portfolio/static/media/footerWallpaper2.24f512b2.jpg);
}

